import React, { useEffect } from 'react';
import useCustomMember from '@hooks/useCustomMember';
import { removeCookie } from '@utils/cookieUtil';
import { toast } from 'react-toastify';
import useCustomWorkspace from '@hooks/useCustomWorkspace';

const RootRedirect: React.FC = () => {
  const { isLogin, memberState, deleteSlices, moveToLogin, isInitialLoading, updateUrlAfterLogin } = useCustomMember();
  const { workspaceState } = useCustomWorkspace();

  useEffect(() => {
    const checkLoginStatus = async () => {
      if (isInitialLoading) {
        const workspaces = memberState?.workspaces;
        if (
          !isLogin ||
          !workspaceState ||
          !Array.isArray(workspaces) ||
          workspaces.length === 0 ||
          !workspaces[0]?.name
        ) {
          try {
            removeCookie('member');
            await deleteSlices();
            moveToLogin();
          } catch (error) {
            toast.error('Failed to log out');
          }
        }
        updateUrlAfterLogin().then();
      }
    };
    checkLoginStatus().catch((error) => console.error('Error in checkLoginStatus:', error));
  }, [
    deleteSlices,
    isInitialLoading,
    isLogin,
    memberState?.workspaces,
    moveToLogin,
    updateUrlAfterLogin,
    workspaceState,
  ]);

  if (isInitialLoading) {
    return <div>Loading...</div>;
  }
};

export default RootRedirect;
